import React, {
  useState,
  useEffect,
  useCallback
} from 'react';
import { Link as RouterLink } from 'react-router-dom';
import clsx from 'clsx';
import moment from 'moment';
import PropTypes from 'prop-types';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  Button,
  Card,
  Checkbox,
  Divider,
  IconButton,
  InputAdornment,
  SvgIcon,
  Tab,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  Tabs,
  TextField,
  Typography,
  Grid,
  makeStyles, Chip
} from '@material-ui/core';
import {
  Edit as EditIcon,
  ArrowRight as ArrowRightIcon,
  Search as SearchIcon
} from 'react-feather';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import {listAllClasses} from '../../../api/classes'
import Label from '../../../components/Label';
import { deleteQuizTest } from '../../../api/quiz';

const tabs = [
  {
    value: 'all',
    label: 'Tất cả'
  }
];
const class_init = [
  {
    code:"",
    name:""
  }
]
const sortOptions = [
  {
    value: 'name|asc',
    label: 'Tên học sinh (tăng)'
  },
  {
    value: 'name|desc',
    label: 'Tên học sinh (giảm)'
  }
];

const applyFilters = (products, query, filters) => {
  return products.filter((product) => {
    let matches = true;

    if ( typeof(query)!=='undefined') {
      const properties = ['email', 'name'];
      let containsQuery = false;

      properties.forEach((property) => {
        if (product[property] && product[property].toLowerCase().includes(query.toLowerCase())) {
          containsQuery = true;
        }
      });

      if (!containsQuery) {
        matches = false;
      }
    }

    Object.keys(filters).forEach((key) => {
      const value = filters[key];

      if (value && product[key] !== value) {
        matches = false;
      }
    });

    return matches;
  });
};

const applyPagination = (products, page, limit) => {
  return products.slice(page * limit, page * limit + limit);
};

const descendingComparator = (a, b, orderBy) => {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }

  if (b[orderBy] > a[orderBy]) {
    return 1;
  }

  return 0;
};

const getComparator = (order, orderBy) => {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
};

const applySort = (products, sort) => {
  const [orderBy, order] = sort.split('|');
  const comparator = getComparator(order, orderBy);
  const stabilizedThis = products.map((el, index) => [el, index]);

  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);

    if (order !== 0) return order;

    return a[1] - b[1];
  });

  return stabilizedThis.map((el) => el[0]);
};

const useStyles = makeStyles((theme) => ({
  root: {},
  queryField: {
    width: 500
  },
  bulkOperations: {
    position: 'relative'
  },
  bulkActions: {
    paddingLeft: 4,
    paddingRight: 4,
    marginTop: 6,
    position: 'absolute',
    width: '100%',
    zIndex: 2,
    backgroundColor: theme.palette.background.default
  },
  bulkAction: {
    marginLeft: theme.spacing(2)
  },
  avatar: {
    height: 42,
    width: 42,
    marginRight: theme.spacing(1)
  }
}));

const Results = ({ className, products, ...rest }) => {
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();

  const [currentTab, setCurrentTab] = useState('all');
  const [selectedQuiz, setSelectedQuiz] = useState([]);
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [query, setQuery] = useState('');
  const [sort, setSort] = useState(sortOptions[0].value);
  const [filters, setFilters] = useState({
    hasAcceptedMarketing: null,
    isProspect: null,
    isReturning: null
  });

  const handleTabsChange = (event, value) => {
    const updatedFilters = {
      ...filters,
      hasAcceptedMarketing: null,
      isProspect: null,
      isReturning: null
    };

    if (value !== 'all') {
      updatedFilters[value] = true;
    }

    setFilters(updatedFilters);
    setSelectedQuiz([]);
    setCurrentTab(value);
  };

  const handleQueryChange = (event) => {
    event.persist();
    setQuery(event.target.value);
  };

  const handleSortChange = (event) => {
    event.persist();
    setSort(event.target.value);
  };

  const handleSelectAllQuiz = (event) => {
    setSelectedQuiz(event.target.checked
      ? products.map((product) => product.id)
      : []);
  };

  const handleSelectOneQuizTest = (event, productId) => {
    if (!selectedQuiz.includes(productId)) {
      setSelectedQuiz((prevSelected) => [...prevSelected, productId]);
    } else {
      setSelectedQuiz((prevSelected) => prevSelected.filter((id) => id !== productId));
    }
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(parseInt(event.target.value));
  };

  const filteredClasses = applyFilters(products, query, filters);
  const sortedClasses = applySort(filteredClasses, sort);
  const paginatedQuizTest = applyPagination(sortedClasses, page, limit);
  const enableBulkOperations = selectedQuiz.length > 0;
  const selectedSomeClasses = selectedQuiz.length > 0 && selectedQuiz.length < products.length;
  const selectedAllClasses = selectedQuiz.length === products.length;
  const getTagsLabel = (tags) => {
    const map = {
      'lop-6': {
        text: 'Lớp 6',
        color: 'error'
      },
      'lop-7': {
        text: 'Lớp 7',
        color: 'success'
      },
      'lop-8': {
        text: 'Lớp 8',
        color: 'warning'
      },
      'lop-9': {
        text: 'Lớp 9',
        color: 'primary'
      }
    };

    const { text, color } = map[tags];

    return (
      <Label color={color}>
        {text}
      </Label>
    );
  };
  const handleDelete = () => {
    console.log(selectedQuiz);
    deleteQuizTest(selectedQuiz);

  };


  return (
    <Card className={clsx(classes.root, className)}{...rest}>
      <Tabs onChange={handleTabsChange} scrollButtons="auto" textColor="secondary" value={currentTab} variant="scrollable">
        {tabs.map((tab) => (
          <Tab key={tab.value} value={tab.value} label={tab.label}/>
        ))}
      </Tabs>
      <Divider />
      <Box p={2} minHeight={56} display="flex" alignItems="center">
        <TextField className={classes.queryField}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SvgIcon fontSize="small" color="action">
                  <SearchIcon />
                </SvgIcon>
              </InputAdornment>
            )
          }}
          onChange={handleQueryChange} placeholder="Tìm kiếm theo tên quiz" value={query} variant="outlined"/>

        <Box flexGrow={1} />
      </Box>

      {enableBulkOperations && (
        <div className={classes.bulkOperations}>
          <div className={classes.bulkActions}>
            <Checkbox checked={selectedAllClasses} indeterminate={selectedSomeClasses} onChange={handleSelectAllQuiz}/>
            <Button variant="outlined" className={classes.bulkAction} onClick={handleDelete}>
              Xóa
            </Button>
            <Button variant="outlined" className={classes.bulkAction}>
              Sửa
            </Button>
          </div>
        </div>
      )}

      <PerfectScrollbar>
        <Box minWidth={700}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox">
                  <Checkbox checked={selectedAllClasses} indeterminate={selectedSomeClasses} onChange={handleSelectAllQuiz}/>
                </TableCell>
                <TableCell>
                  Tên
                </TableCell>
                <TableCell>
                  Loại Quiz
                </TableCell>
                <TableCell>
                  Tags
                </TableCell>
                <TableCell>
                  List câu hỏi
                </TableCell>
                <TableCell align="right">
                  Actions
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedQuizTest.map((quiz) => {
                const isQuizTestSelected = selectedQuiz.includes(quiz.id);
                return (
                  <TableRow hover key={quiz.id} selected={isQuizTestSelected}>

                  <TableCell padding="checkbox">
                    <Checkbox checked={isQuizTestSelected} onChange={(event) => handleSelectOneQuizTest(event, quiz.id)} value={isQuizTestSelected}/>
                  </TableCell>

                  <TableCell>
                  {quiz.name}
                  </TableCell>

                  <TableCell>
                    {quiz.typeQuiz}
                  </TableCell>

                  <TableCell>
                  {quiz.tags.map((s) =>
                    getTagsLabel(s)
                  )}
                  </TableCell>

                  <TableCell>
                    {quiz.listQuiz.map((chip) =>
                      (<Chip
                        className={chip}
                        key={chip}
                        label={chip}/>)
                    )}
                  </TableCell>


                  <TableCell align="right">
                    <IconButton component={RouterLink} to={"/hbon/management/quiztest/edit/"+ quiz.id}>
                      <SvgIcon fontSize="small">
                        <EditIcon />
                      </SvgIcon>
                    </IconButton>
                  </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={filteredClasses.length}
        onChangePage={handlePageChange}
        onChangeRowsPerPage={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
    </Card>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  products: PropTypes.array.isRequired
};

Results.defaultProps = {
  products: []
};

export default Results;
