import React, {
  useState,
  useEffect,
  useCallback
} from 'react';
import {
  Box,
  Container,
  makeStyles
} from '@material-ui/core';
import Page from 'src/components/Page';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import Header from './Header';
import Results from './Results';
import {listAllQuiztests} from '../../../api/quiz'

import  { Redirect } from 'react-router-dom'
import useAuth from 'src/hooks/useAuth';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

const QuizTestListView = () => {
  const { user} = useAuth();
  const renderRedirect = () => {
    if(user && user.roles && user.roles.some(el => el === "hbonadmin")){
        console.log("admin");
      }
      else {
        console.log("khong co quyen");
        return <Redirect to='/404' />
      }
  }
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [quiztest, setQuizTest] = useState([]);

  const getQuizTest = useCallback(async () => {
    try {
      const products = await listAllQuiztests();
      if (isMountedRef.current) {
        setQuizTest(products);
        console.log(products)
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {
    getQuizTest();
  }, [getQuizTest]);

  return (
    <Page className={classes.root} title="Hbon Manager">
      {renderRedirect()}
      <Container maxWidth={false}>
        <Header />
        <Box mt={3}>
          <Results products={quiztest} />
        </Box>
      </Container>
    </Page>
  );
};

export default QuizTestListView;
